<template>
  <v-app light>
  <v-app-bar dense light color="primary">
    <v-app-bar-nav-icon light class="white--text" @click.stop="drawer=!drawer"></v-app-bar-nav-icon>
    <v-toolbar-title class="white--text">News App</v-toolbar-title>
  </v-app-bar>

  <v-navigation-drawer v-model="drawer" absolute temporary class="drawer-style" id="style-1">
    <v-list nav dense class="pt-3 white--text">
      <v-list-item
        v-for="source in sources"
        :key="source.id"
        @click="selectSource(source.id)">

        <v-list-item-icon>
          <v-icon size="32px" class="img-circle elevation-7 mb-1" :src="getImgUrl(source.id)"></v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title class="white--text">{{ source.name }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>

    </v-list>
  </v-navigation-drawer>

  <v-main>
    <v-container fluid>
      <MainContent :articles="articles"></MainContent>  
    </v-container>
  </v-main>

  <v-footer class="secondary" app>
    <v-layout row wrap align-center>
      <v-flex xs12>
        <div class="white--text my-3 ml-5">
            Made with
            <a class="blue--text" href="https://vuetifyjs.com" target="_blank">Vuetify</a>
            by <a class="white--text" href="https://github.com/nadiraihanah/" target="_blank">nadiraihanah</a>
        </div>
      </v-flex>
    </v-layout>
  </v-footer>
</v-app>

</template>

<script>
import axios from 'axios'
import MainContent from './components/MainContent.vue' 

export default {

  components: {
    MainContent
  },

  data() {
    return {
      drawer: true,
      api_key:'fb4bf92b92674b6f98bc9ead3ad2735b', 
      articles: [],
      sources: []
    }
  },

  created () {
    this.fetchArticle(),
    this.fetchSource()
  },

  methods: {
    fetchArticle(){
    axios.get('https://newsapi.org/v2/everything?q=movie&language=en&apiKey='+this.api_key)
      .then(response => {
        this.articles = response.data.articles
        console.log('data article:')
        console.log(response.data.articles)
      })
    },

    fetchSource(){
      axios.get('https://newsapi.org/v2/sources?language=en&apiKey='+this.api_key)
      .then(response => {
        this.sources = response.data.sources
        console.log('data source:')
        console.log(response.data.sources)
      })
    },

    getImgUrl(pic) {
      return ('../assets/images/'+pic+'.png')
    },

      selectSource(source){
        this.$emit('selectsource',source)
      }
  }

  }
</script>

<style scoped>
  #style-1::-webkit-scrollbar {
    width: 6px;
    background-color: #F5F5F5;
  }
  #style-1::-webkit-scrollbar-thumb {
    background-color: #F90; 
    background-image: -webkit-linear-gradient(90deg, rgba(255, 255, 255, .2) 25%,
      transparent 25%,
      transparent 50%,
      rgba(255, 255, 255, .2) 50%,
      rgba(255, 255, 255, .2) 75%,
      transparent 75%,
      transparent)
  }
  #style-1::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background-color: #F5F5F5;
  }
  .drawer-style {
    overflow: hidden;
    background-color: #2196F3 !important;
    border-color: #2196F3 !important;
